import "./styles.css";
import React from "react";
export default class App extends React.Component {
  state = {
    countPlus: 0,
    countMinus: 0
  };

  add = () => {
    this.setState((prevState) => ({ countPlus: prevState.countPlus + 1 }));
  };

  subtract = () => {
    this.setState((prevState) => ({ countMinus: prevState.countMinus - 1 }));
  };
  render() {
    return (
      <div className="outer-div">
        <div className="inner-div">
          <span>
            <h2>
              Вы за благотворительную деятельность в развитии образования?
            </h2>
          </span>
          <div className="left-div" onClick={this.add}>
            <a href="#">
              <img src="heart.png" />
            </a>
            <h2> {this.state.countPlus}</h2>
          </div>
          <div className="right-div" onClick={this.subtract}>
            <a href="#">
              <img src="neg.png" />
            </a>
            <h2>{this.state.countMinus}</h2>
          </div>
        </div>
      </div>
    );
  }
}
